	module.exports = {"data":{"post":{"html":"<p>PapaScott Headquarters is moving starting today... from the upstairs office to the basement guest room. The office is just too tight on space for both me and Mama to work and for Christopher to play at the same time. And a wireless card should enable us to continue to share our Internet connection... at least I don't think there's too much steel and concrete in the way. Otherwise <span style=\"text-decoration: line-through;\">Mama will have to do without,</span> er, I mean I'll have to run a cable to connect the rooms.</p>\n<p>Anyway, first step is to move 4 bookshelves with about 20 m of <a href=\"http://www.ora.com\">animal books</a> (and a few others) down 2 flights of stairs.</p>","fields":{"title":"Pack Mule","date":"06 Jan 2002","url":"https://papascott.de/2002-01-06-pack-mule/"}}},"pathContext":{"slug":"/2002-01-06-pack-mule/","prev":{"fields":{"title":"Game Over","date":"05 Jan 2002","slug":"/2002-01-05-game-over/"}},"next":{"fields":{"title":"U7","date":"07 Jan 2002","slug":"/2002-01-07-u7/"}}}}

